/*Typing Loader*/
.typing_loader{
    -webkit-align-self: center;
            align-self: center;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    -webkit-animation: typing 1s linear infinite;
            animation: typing 1s linear infinite;  
    position: relative;
    left: -24px;
}
@-webkit-keyframes typing{
    0%{
        background-color: rgba(0,0,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,0.2), 
                    24px 0px 0px 0px rgba(0,0,255,0.2);
      }
    25%{ 
        background-color: rgba(0,0,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,2), 
                    24px 0px 0px 0px rgba(0,0,255,0.2);
    }
    75%{ background-color: rgba(0,0,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,0.2), 
                    24px 0px 0px 0px rgba(0,0,255,1);
    }
    100%{
        background-color: rgba(0,0,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,0.2), 
                    24px 0px 0px 0px rgba(0,0,255,0.2);
    }
}

@keyframes typing{
   0%{
        background-color: rgba(0,0,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,0.2), 
                    24px 0px 0px 0px rgba(0,0,255,0.2);
      }
    25%{ 
        background-color: rgba(0,0,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,2), 
                    24px 0px 0px 0px rgba(0,0,255,0.2);
    }
    75%{ background-color: rgba(0,0,255, 0.4);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,0.2), 
                    24px 0px 0px 0px rgba(0,0,255,1);
      }
      100%{
        background-color: rgba(0,0,255, 1);
        box-shadow: 12px 0px 0px 0px rgba(0,0,255,0.2), 
                    24px 0px 0px 0px rgba(0,0,255,0.2);
    }
}

.loader {
  border: 8px solid #f3f3f3; /* Light grey */
  border-top: 8px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  margin-left: 10px;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.switch input { 
    display:none;
}
.switch {
    display:inline-block;
    width:60px;
    height:30px;
    margin:8px;
    -webkit-transform:translateY(50%);
            transform:translateY(50%);
    position:relative;
}

.slider {
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    border-radius:30px;
    box-shadow:0 0 0 2px #777, 0 0 4px #777;
    cursor:pointer;
    border:4px solid transparent;
    overflow:hidden;
    transition:.4s;
}
.slider:before {
    position:absolute;
    content:"";
    width:100%;
    height:100%;
    background:#777;
    border-radius:30px;
    -webkit-transform:translateX(-30px);
            transform:translateX(-30px);
    transition:.4s;
}

input:checked + .slider:before {
    -webkit-transform:translateX(30px);
            transform:translateX(30px);
    /*background:limeGreen;*/
}
